/*** LAYOUT/CONTENT ***/
:root {
  --bs-indigo: #6610f2;
  --bs-purple: #6f42c1;
  --bs-pink: #e83e8c;
  --bs-red: #dc3545;
  --bs-orange: #fd7e14;
  --bs-yellow: #ffc107;
  --bs-green: #28a745;
  --bs-teal: #20c997;
  --bs-cyan: #17a2b8;
  --bs-gray: #9aa0a6;
  --bs-gray-dark: #2e2e2e;
  --bs-primary: #2250fc;
  --bs-secondary: #c1c3ce;
  --bs-success: #28a745;
  --bs-info: #17a2b8;
  --bs-warning: #ffc107;
  --bs-danger: #dc3545;
  --bs-white: #fff;
  --bs-light: #fbfbfb;
  --bs-dark: #1e2022;
  --bs-blue: #007bff;
  --bs-font-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --bs-font-monospace: SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
  --bs-gradient: linear-gradient(
    180deg,
    hsla(0, 0%, 100%, 0.15),
    hsla(0, 0%, 100%, 0)
  );
}
*,
:after,
:before {
  box-sizing: border-box;
}
:root {
  font-size: 14px;
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}
*,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  text-decoration: none;
}
article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
  display: block;
}
html {
  font-size: 88%;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #fff;
  color: #3c4043;
  font-family: Poppins, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  margin: 0;
}

p {
  color: #3c4043;
  font-family: Poppins, sans-serif;
  font-size: 1rem;
  font-style: normal;
  letter-spacing: 0;
  line-height: 1.7;
  text-transform: none;
  font-weight: 400;
  margin-bottom: 1rem;
  margin-top: 0;
}
ul.bullets {
  list-style: inside;
}
strong {
  font-weight: 600;
}
del {
  -webkit-text-decoration-line: line-through;
  text-decoration-line: line-through;
}
a:not(.btn):not(.badge):not(.btn):not([href]):not([tabindex]) {
  color: #3c4043;
}
ol,
ul {
  padding-left: 2rem;
}
dl,
ol,
ul {
  margin-bottom: 1rem;
  margin-top: 0;
}
.small,
small {
  font-size: 0.875em;
}
.container,
.container-fluid,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-left: var(--bs-gutter-x, 1rem);
  padding-right: 1rem;
  padding-right: var(--bs-gutter-x, 1rem);
  width: 100%;
}
.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--bs-gutter-x) * -0.5);
  margin-right: calc(var(--bs-gutter-x) * -0.5);
  margin-top: calc(var(--bs-gutter-y) * -1);
}
.row > * {
  flex-shrink: 0;
  margin-top: var(--bs-gutter-y);
  max-width: 100%;
  width: 100%;
}

