/*** NAV ***/

#mainMenu .container {
  max-width: 100% !important;
}
#mainMenu .nav-link {
  border: 0 solid transparent;
  border-radius: 0;
  color: #000;
  float: left;
  font-family: Poppins, sans-serif;
  font-size: 1.2em;
  font-weight: 700;
  letter-spacing: 0.4px;
  line-height: normal;
  margin-left: 6px;
  padding: 10px 12px;
  position: relative;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
#mainMenu .dropdown-toggle.nav-link {
  color: #000;
}
#mainMenu .navbar {
  padding-right: 4%;
}
#mainMenu .nav-link:hover {
  color: #2250fc;
}
.header-inner {
  box-shadow: 0 12px 6px rgba(0, 0, 0, 0.06);
  height: 80px;
  left: 0;
  right: 0;
  transition: all 0.4s ease-in-out;
}
.dropdown {
  display: inline-block;
}
.dropdown-toggle:after,
.nav-link:after {
  border: 0;
  display: none;
  margin: 0;
}
.dropdown-submenu > .dropdown-item:not(.no-icon):not(.rounded-circle),
.dropdown-toggle:not(.no-icon):not(.rounded-circle) {
  padding: 0.5rem 1.5rem;
  position: relative;
}
.dropdown-submenu > .dropdown-item:not(.no-icon):not(.rounded-circle):after,
.dropdown-toggle:not(.no-icon):not(.rounded-circle):after {
  border: 0;
  border-right: 0.0625rem solid #9aa0a6;
  border-top: 0.0625rem solid #9aa0a6;
  content: "";
  height: 0.375rem;
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
  width: 0.375rem;
}
.dropdown > .btn:not(.no-icon):not(.rounded-circle) {
  padding-right: 1.65rem;
}
.dropdown-item {
  font-size: 1rem;
  font-weight: 600;
}
.nav-link.active,
.nav-link:hover {
  color: #3c4043;
}
#mainMenu,
header {
  z-index: 1000;
}
.navbar-dark .navbar-toggler {
  border-color: #1e2022 !important;
  color: #1e2022 !important;
}
.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3E%3Cpath stroke='rgba(0, 0, 0, 1)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}
.menu-dark {
  background-color: #fff;
}
.collapse:not(.show) {
  display: none;
}
.collapsing {
  height: 0;
  overflow: hidden;
  transition: height 0.2s ease;
}
.dropdown,
.dropend,
.dropstart,
.dropup {
  position: relative;
}
.dropdown-toggle {
  white-space: nowrap;
}
.dropdown-toggle:after {
  border-bottom: 0;
  border-left: 0.3em solid transparent;
  border-right: 0.3em solid transparent;
  border-top: 0.3em solid;
  content: "";
  display: inline-block;
  margin-left: 0.255em;
  vertical-align: 0.255em;
}
.dropdown-toggle:empty:after {
  margin-left: 0;
}
.dropdown-menu {
  background-clip: padding-box;
  background-color: #fff;
  border: 1px solid #e4e6ef;
  border-radius: 0.42rem;
  box-shadow: 0 0.625rem 2.5rem 0.625rem rgba(140, 152, 164, 0.175);
  color: #3c4043;
  display: none;
  font-size: 0.875rem;
  list-style: none;
  margin: 0;
  min-width: 11rem;
  padding: 1rem 0;
  position: absolute;
  text-align: left;
  z-index: 1000;
}
.dropdown-menu[data-bs-popper] {
  left: 0;
  margin-top: 0.125rem;
  top: 100%;
}
.dropdown-item {
  background-color: transparent;
  border: 0;
  clear: both;
  color: #1e2022;
  display: block;
  font-weight: 400;
  padding: 0.5rem 1.5rem;
  text-align: inherit;
  white-space: nowrap;
  width: 100%;
}
.dropdown-item:focus,
.dropdown-item:hover {
  background-color: #e4e6ef;
  color: #1e2022;
}
.dropdown-item.active,
.dropdown-item:active {
  background-color: none;
  color: none;
  text-decoration: none;
}
.dropdown-item.disabled,
.dropdown-item:disabled {
  background-color: transparent;
  color: #9aa0a6;
  pointer-events: none;
}
.dropdown-menu.show {
  display: block;
}
.nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin-bottom: 0;
  padding-left: 0;
}
.nav-link {
  color: #2250fc;
  display: block;
  font-size: 1rem;
  padding: 0.25rem 1rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
}
@media (prefers-reduced-motion: reduce) {
  .nav-link {
    transition: none;
  }
}
.nav-link:focus,
.nav-link:hover {
  color: #1d44d6;
}
.nav-link.disabled {
  color: #9aa0a6;
  cursor: default;
  pointer-events: none;
}
.nav-tabs {
  border-bottom: 1px solid #e4e6ef;
}
.nav-tabs .nav-link {
  background: none;
  border: 1px solid transparent;
  border-top-left-radius: 0.42rem;
  border-top-right-radius: 0.42rem;
  margin-bottom: -1px;
}
.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover {
  border-color: #c1c3ce #c1c3ce #e4e6ef;
  isolation: isolate;
}
.nav-tabs .nav-link.disabled {
  background-color: transparent;
  border-color: transparent;
  color: #9aa0a6;
}
.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  background-color: #fff;
  border-color: #c1c3ce #c1c3ce #fff;
  color: #2e2e2e;
}
.nav-tabs .dropdown-menu {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin-top: -1px;
}
.nav-pills .nav-link {
  background: none;
  border: 0;
  border-radius: 0.42rem;
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background-color: #2250fc;
  color: #fff;
}
.nav-fill .nav-item,
.nav-fill > .nav-link {
  flex: 1 1 auto;
  text-align: center;
}
.nav-justified .nav-item,
.nav-justified > .nav-link {
  flex-basis: 0;
  flex-grow: 1;
  text-align: center;
}
.nav-fill .nav-item .nav-link,
.nav-justified .nav-item .nav-link {
  width: 100%;
}
.tab-content > .tab-pane {
  display: none;
}
.tab-content > .active {
  display: block;
}
.navbar {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0.75rem 0;
  position: relative;
}
.navbar > .container,
.navbar > .container-fluid,
.navbar > .container-lg,
.navbar > .container-md,
.navbar > .container-sm,
.navbar > .container-xl,
.navbar > .container-xxl {
  align-items: center;
  display: flex;
  flex-wrap: inherit;
  justify-content: space-between;
}
.navbar-brand {
  font-size: 1.25rem;
  margin-right: 1rem;
  padding-bottom: 0.0625rem;
  padding-top: 0.0625rem;
  white-space: nowrap;
}
.navbar-brand.mw-logo {
  color: #1e2022;
  font-family: inherit;
  font-size: 40px;
  font-weight: 800;
  letter-spacing: 1.5px;
}
.navbar-nav {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin-bottom: 0;
  padding-left: 0;
}
.navbar-nav .nav-link {
  padding-left: 0;
  padding-right: 0;
}
.navbar-nav .dropdown-menu {
  position: static;
}
.navbar-text {
  padding-bottom: 0.25rem;
  padding-top: 0.25rem;
}
.navbar-collapse {
  align-items: center;
  flex-basis: 100%;
  flex-grow: 1;
}
.navbar-toggler {
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 0.42rem;
  font-size: 1.25rem;
  line-height: 1;
  padding: 0.25rem 0.75rem;
  transition: box-shadow 0.15s ease-in-out;
}
@media (prefers-reduced-motion: reduce) {
  .navbar-toggler {
    transition: none;
  }
}
.navbar-toggler:hover {
  text-decoration: none;
}
.navbar-toggler:focus {
  box-shadow: 0 0 0 none;
  outline: 0;
  text-decoration: none;
}
.navbar-toggler-icon {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100%;
  display: inline-block;
  height: 1.5em;
  vertical-align: middle;
  width: 1.5em;
}
.search-navbar {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  min-width: 50%;
}

.search-main {
  display: none;
}

@media (min-width: 1200px) {
  .navbar-expand-xl {
    flex-wrap: nowrap;
    justify-content: flex-start;
  }
  .navbar-expand-xl .navbar-nav {
    flex-direction: row;
  }
  .navbar-expand-xl .navbar-nav .dropdown-menu {
    position: absolute;
  }
  .navbar-expand-xl .navbar-nav .nav-link {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  .navbar-expand-xl .navbar-nav-scroll {
    overflow: visible;
  }
  .navbar-expand-xl .navbar-collapse {
    display: flex !important;
    flex-basis: auto;
  }
  .navbar-expand-xl .navbar-toggler {
    display: none;
  }
}
.navbar-light .navbar-brand,
.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:hover {
  color: rgba(0, 0, 0, 0.9);
}
.navbar-light .navbar-nav .nav-link {
  color: rgba(0, 0, 0, 0.55);
}
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: rgba(0, 0, 0, 0.7);
}
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .show > .nav-link {
  color: rgba(0, 0, 0, 0.9);
}
.navbar-light .navbar-toggler {
  border-color: rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.55);
}
.navbar-dark .navbar-nav .nav-link {
  color: hsla(0, 0%, 100%, 0.55);
}
.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:hover {
  color: hsla(0, 0%, 100%, 0.75);
}
.navbar-dark .navbar-nav .nav-link.disabled {
  color: hsla(0, 0%, 100%, 0.25);
}
.navbar-dark .navbar-nav .nav-link.active,
.navbar-dark .navbar-nav .show > .nav-link {
  color: #fff;
}
.navbar-dark .navbar-toggler {
  border-color: hsla(0, 0%, 100%, 0.1);
  color: hsla(0, 0%, 100%, 0.55);
}
.tabs .nav-tabs {
  border-bottom: 2px solid #e4e6ef;
  margin-bottom: 30px;
}
.tabs .nav-tabs .nav-item {
  margin-bottom: -2px;
}
.tabs .nav-tabs .nav-link {
  border: 0;
  border-bottom: 2px solid #e4e6ef;
  border-top-left-radius: 0.42rem;
  border-top-right-radius: 0.42rem;
