  padding: 14px 40px;
}
.btn:not(.close):not(.mfp-close):focus,
.btn:not(.close):not(.mfp-close):hover,
.btn:not(.close):not(.mfp-close):not(:disabled):not(.disabled).active,
.btn:not(.close):not(.mfp-close):not(:disabled):not(.disabled):active,
